    5: { 1: "ගු", 2: "ගු ", 3: "ගු" },
    6: { 1: "ගු", 2: "ගු ", 3: "ගු" },
    7: { 1: "ගු", 2: "ගු ", 3: "ගු" },
    8: { 1: "ගු", 2: "ගු ", 3: "ගු" },
    9: { 1: "ගු", 2: "ගු ", 3: "ගු" },
    10: { 1: "ගු", 2: "ගු ", 3: "ගු" },
